import React from 'react';

const Result = ({total,term,amount}) => (
    <div className="u-full-width result">
        <h2>Results</h2>
        <p>Amount requested: ${amount}</p>
        <p>Term: {term} months</p>
        <p>Total amount to repay: ${ (total).toFixed(2) }</p>
        <p>Montly payment: ${ (total/term).toFixed(2) }</p>
    </div>
);
 
export default Result;