import React,{Fragment,useState} from 'react';
import {calculateAmount} from '../helpers';

const Form = (props) => {

    const {amount,saveAmount,term,saveTerm,saveTotal,saveLoading} = props

    const [error,saveError] = useState(false);

    const calculateLoan = e => {
        e.preventDefault();
        
        //Validation
        if(amount === 0 || term === ''){
            saveError(true);
        }
        saveError(false);

        saveLoading(true);
        setTimeout(() => {
            const total = calculateAmount(amount,term);

            saveTotal(total);

            saveLoading(false);
        }, 3000);

        
    }

    return ( 
        <Fragment>    
            <form onSubmit={calculateLoan}>
              <div className="row">
                  <div>
                      <label>Loan Amount</label>
                      <input 
                          className="u-full-width" 
                          type="number" 
                          placeholder="i.e.: 3000" 
                          onChange={ (e)=> saveAmount( parseInt(e.target.value) ) }
                      />
                  </div>
                  <div>
                      <label>Terms</label>
                      <select 
                          className="u-full-width"
                          onChange={ (e)=> saveTerm( parseInt(e.target.value) ) }
                      >
                          <option value="">Select</option>
                          <option value="3">3 months</option>
                          <option value="6">6 months</option>
                          <option value="12">12 months</option>
                          <option value="24">24 months</option>
                      </select>
                  </div>
                  <div>
                      <input 
                          type="submit" 
                          value="Calculate" 
                          className="button-primary u-full-width" 
                      />
                  </div>
              </div>
            </form>

            { (error) ? <p className="error">All fields required</p> : null }
            
        </Fragment>
    );
}
 
export default Form;